<template>
    <header class="head">
			<a class="head-left"><i class="icon-back"></i>返回</a>
			<h1>
				<Process :time="time" @gameOver='gameOver' v-if="!isBegTime"></Process>
			</h1>
			<a class="head-right">{{now}}/{{total}}</a>
		</header>
</template>

<script>
import Process from "./Process";
export default {
  components: {
    Process
  },
  props: ["time", "now", "total", "isBegTime"],
  computed: {},
  methods: {
    gameOver() {
      this.$emit("gameOver");
    }
  }
};
</script>

<style>

</style>



