<template>
  <div id="app">
    <Head 
    :time="time" 
    @gameOver="gameOver" 
    :now="count+1" 
    :total="questionArr.length"
    :isBegTime="isShow"
    >
    </Head>
    <section>
      <div class="topt">
        <!-- <div class="time"><span></span><i></i></div> -->
        <!-- <h5 class="yeq">1/15</h5> -->
      </div>
      <Select 
      v-if="question.quesType==1" 
      :question="question"
      @next="addCount"
      ></Select>
      <TianKong 
      v-if="question.quesType==2" 
      :question="question" 
      @next="addCount"></TianKong>
    </section>
    <Dialog 
    v-if="isShow"
    :gonggao="gonggao"
    @startGame="startGame"
    >
    </Dialog>
  </div>
</template>

<script>
function GetQueryString(name) {
  var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)");
  var r = window.location.search.substr(1).match(reg);
  if (r != null) return decodeURI(unescape(r[2]));
  return null;
}
var stageid = GetQueryString("stageid");
import Head from "./components/Head";
import Select from "./components/Select";
import TianKong from "./components/TianKong";
import Dialog from "./components/Dialog";

export default {
  name: "App",
  components: {
    Head,
    Select,
    TianKong,
    Dialog
  },
  data() {
    return {
      questionArr: [],
      question: {
        id: "",
        quesType: "",
        info: ""
      },
      isShow: true,
      gonggao: {
        keypoint: "",
        example: "",
        thinking: ""
      },
      time: 20, //时间
      count: 0, //从第几道题开始
      // now: 1,
      isGameOver: false
    };
  },
  methods: {
    getQuestion() {
      var self = this;
      this.$http
        // .get("api/getQues")
        .get("../Index/getQuesView?stageid=" + stageid)
        .then(function(response) {
          console.log("xx");
          if (
            response.data.stage[0].keypoint == "" ||
            response.data.stage[0].example == "" ||
            response.data.stage[0].thinking == ""
          ) {
            self.isShow = false;
          } else {
            self.isShow = true;
            self.gonggao.keypoint = response.data.stage[0].keypoint;
            self.gonggao.example = response.data.stage[0].example;
            self.gonggao.thinking = response.data.stage[0].thinking;
          }
          self.time = response.data.stage[0].totaltime * 1;
          for (var i in response.data.ques) {
            self.questionArr.push(response.data.ques[i]);
          }
          self.question.id = response.data.ques[self.count].id;
          self.question.quesType = response.data.ques[self.count].questype;
          self.question.info = eval(
            "(" + response.data.ques[self.count].content + ")"
          );
        })
        .catch(function(response) {
          console.log(response);
        });
    },
    getNextQuestion() {
      this.question.id = this.questionArr[this.count].id;
      this.question.quesType = this.questionArr[this.count].questype;
      this.question.info = eval(
        "(" + this.questionArr[this.count].content + ")"
      );
    },
    addCount(id, isRight) {
      console.log("答案id" + id + "--" + isRight);
      // return false;
      var that = this;
      if (this.count < this.questionArr.length - 1) {
        setTimeout(function() {
          that.count++;
          if (!that.isGameOver) {
            that.getNextQuestion();
          }
        }, 1000);
      } else {
        alert("jieshu");
      }
    },
    //时间到
    gameOver() {
      alert("gameOver");
      this.isGameOver = true;
    },
    //开始游戏
    startGame() {
      this.isShow = false;
    }
  },
  mounted() {
    this.getQuestion();
  }
};
</script>

<style>

</style>



